import { useEffect } from 'react';
import { useCookies } from 'react-cookie';

const Logout = () => {
  const [cookies, setCookie, removeCookies] = useCookies(['token']);
  
  const handleLogout = () => {
    // Xa cookie c tn 'token'
    removeCookies('token');
  };

  useEffect(() => {
    if (!cookies.token) {
      handleLogout();
      localStorage.removeItem('cart'); // Xa localStorage khi ng xut
    }
  }, [cookies.token]);

  return handleLogout;
};

export default Logout;
